@import url(https://fonts.googleapis.com/css2?family=Inter&family=Sora&display=swap);
.button {
  background: none;
  border: none;
  outline: none;
  border-bottom: 0.3rem solid var(--info-color);
  padding: 0 0.8rem;
  padding-right: 1rem;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s ease-out 0s;
  box-shadow: 0 0.3125rem 0.625rem -0.5rem #000000;
  margin: 0.5rem 0.3rem;
  color: var(--black-color);
}

.button:hover {
  color: var(--white-color);
}

.button .icon {
  margin-right: 0.3rem;
}

.button.bg-success {
  border-bottom: 0.3rem solid var(--success-color);
}

.button.bg-danger {
  border-bottom: 0.3rem solid var(--danger-color);
}

.button.bg-info {
  border-bottom: 0.3rem solid var(--info-color);
}

.button.bg-success:hover {
  background-color: var(--success-color);
}

.button.bg-danger:hover {
  background-color: var(--danger-color);
}

.button.bg-info:hover {
  background-color: var(--info-color);
}

.fail-section {
  color: var(--danger-color);
  margin-bottom: 0.5rem;
}

.fail-item {
  margin-left: 0.3rem;
}

.section-item.view h1 {
  font-size: 1.3rem;
}

.section-item.view p {
  font-size: 1.7rem;
  font-family: var(--primary-font);
  color: var(--black-color);
  margin-left: 0.5rem;
}

.section-item label {
  display: block;
  font-family: var(--primary-font);
  font-size: 1.1rem;
  color: var(--black-color);
}

.section-item .icon {
  color: var(--black-color);
  width: 2rem;
  height: auto;
}

.section-item-input-container {
  border: var(--card-border);
  display: flex;
  width: 100%;
  padding: 0.3rem;
}

.section-item-input-container input,
.section-item-input-container textarea {
  width: 100%;
  max-width: 90%;
  min-width: 90%;
  border: none;
  outline: none;
  font-size: 1.2rem;
  color: var(--black-color);
}

.section-item-input-container textarea {
  min-width: 95%;
}

@media (max-width: 767px) {
  .section-item-input-container {
    width: 100%;
  }
}

.section {
  width: 95% !important;
  margin: 0.3rem auto;
  padding: 2%;
}

.section h4 {
  margin-bottom: 0.5rem;
}

.section-items {
  margin: 1rem 0;
  margin-top: 0;
}

.section-item {
  margin: 0.5rem 0;
}

.section-item [failed='true'] {
  border-color: var(--danger-color);
}

.section-item [failed='false'] {
  border-color: var(--success-color);
}

.parent-section,
.section {
  padding: 2%;
  padding-top: 0;
  width: 80%;
  margin: 0 auto;
  background-color: var(--white-color);
  border-radius: var(--card-border-radius);
  border: var(--card-border);
  margin: 1rem auto;
  box-shadow: var(--card-shadow);
}

header {
  background-color: var(--primary-color);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 10%;
}

header > * {
  color: var(--white-color);
}

header p {
  font-family: var(--secondary-font);
}

header h1 {
  color: var(--white-color);
}

header .logo {
  width: 6rem;
  height: auto;
}

footer {
  background-color: var(--black-color);
  padding: 1rem;
}

footer a {
  color: var(--white-color);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 10%;
  font-family: var(--primary-font);
  font-size: 1.5rem;
}

footer a .icon {
  width: 3rem;
  height: auto;
}

footer a:hover {
  color: var(--primary-color);
}

:root {
  --primary-color: #1e96fc;
  --warning-color: #ffec1c;
  --success-color: #39d352;
  --info-color: #5697ff;
  --danger-color: #ff3a3d;
  --black-color: #242423;
  --white-color: #ffffff;
  --white-secondary-color: #f3f3f4;

  --primary-font: 'Sora', 'Inter', sans-serif;
  --secondary-font: 'Inter', 'Sora', sans-serif;

  --card-shadow: 0px 0.375rem 0.5rem -0.375rem rgba(24, 39, 75, 0.12),
    0px 0.5rem 1rem -0.375rem rgba(24, 39, 75, 0.08);
  --card-border-radius: 1rem;
  --card-border: 0.1rem solid var(--black-color);
}

body {
  background-color: #f3f3f4;
  background-color: var(--white-secondary-color);
}

html {
  font-size: 100%;
}

* {
  font-family: 'Inter', 'Sora', sans-serif;
  font-family: var(--secondary-font);
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
.title {
  font-weight: 400;
  line-height: 1.3;
  font-family: 'Sora', 'Inter', sans-serif;
  font-family: var(--primary-font);
  color: #242423;
  color: var(--black-color);
}

h1 {
  font-size: 3.052rem;
}

h2 {
  font-size: 2.441rem;
}

h3 {
  font-size: 1.953rem;
}

h4 {
  font-size: 1.563rem;
}

h5 {
  font-size: 1.25rem;
}

small,
.text_small {
  font-size: 0.8rem;
}

